.landing-oculus-strap, .landing-body, .landing-table-leg-top{fill:#cbcbcb}
.landing-skin{fill:#ffd7ad}
.landing-table-side{fill:#8b6239}
.landing-sticky-yellow-1{fill:#fbf164}
.landing-sticky-yellow-2{fill:#ccc451}
.landing-sticky-green{fill:#39b44a}
.landing-sticky-pink-1{fill:#ff7bab}
.landing-sticky-pink-2{fill:#cc6289}
.landing-sticky-blue{fill:#29aae1}
.landing-brush-handle, .landing-shoe{fill:#42210b}
.landing-table-leg-side {fill:#989898}
